.tui-pagination {
  height: 44px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  display: flex;
}

.tui-page-btn {
  height: 44px;
  width: 44px;
  color: inherit;
  border: 1px solid #111;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 700;
  display: flex;
}

.tui-page-btn.tui-first, .tui-page-btn.tui-prev {
  color: #fff;
  cursor: pointer;
  background: #111;
  border-color: #111;
}

.tui-page-btn.tui-first:hover, .tui-page-btn.tui-prev:hover {
  filter: drop-shadow(0 4px 4px #00000040);
}

.tui-page-btn.tui-first:active, .tui-page-btn.tui-prev:active {
  color: #eac645;
}

.tui-page-btn.tui-next, .tui-page-btn.tui-last {
  color: #111;
  cursor: pointer;
  background: #eac645;
  border-color: #111;
}

.tui-page-btn.tui-next:hover, .tui-page-btn.tui-last:hover {
  filter: drop-shadow(0 4px 4px #00000040);
}

.tui-page-btn.tui-next:active, .tui-page-btn.tui-last:active {
  color: #fff;
}

.tui-page-btn.tui-is-disabled.tui-first, .tui-page-btn.tui-is-disabled.tui-prev {
  color: #b4afaf;
  cursor: pointer;
  filter: none;
  background: #111;
  border-color: #111;
}

.tui-page-btn.tui-is-disabled.tui-next, .tui-page-btn.tui-is-disabled.tui-last {
  color: #b4afaf;
  cursor: pointer;
  filter: none;
  background: #eac645;
  border-color: #111;
}

.tui-pagination .tui-is-selected, .tui-pagination strong {
  color: #fff;
  cursor: pointer;
  filter: drop-shadow(0 4px 4px #00000040);
  background: #111;
  border-color: #111;
}

.tui-pagination .tui-ico-first, .tui-pagination .tui-ico-prev, .tui-pagination .tui-ico-next, .tui-pagination .tui-ico-last {
  font-size: 0;
}

.tui-ico-prev:before {
  content: "";
  width: 14px;
  height: 14px;
  border: 3px solid;
  border-top: none;
  border-left: none;
  border-radius: 3px;
  margin-left: 5px;
  display: inline-block;
  transform: rotate(.37turn);
}

.tui-ico-first:after {
  content: "";
  width: 14px;
  height: 14px;
  border: 3px solid;
  border-top: none;
  border-left: none;
  border-radius: 3px;
  margin-left: 5px;
  margin-right: 5px;
  display: inline-block;
  transform: rotate(.37turn);
}

.tui-ico-first:before {
  content: "";
  width: 14px;
  height: 14px;
  border: 3px solid;
  border-top: none;
  border-left: none;
  border-radius: 3px;
  display: inline-block;
  transform: translateX(10px)rotate(.37turn);
}

.tui-ico-next:before {
  content: "";
  width: 14px;
  height: 14px;
  border: none;
  border-top: 3px solid;
  border-left: 3px solid;
  border-radius: 3px;
  margin-right: 5px;
  display: inline-block;
  transform: rotate(.37turn);
}

.tui-ico-last:before {
  content: "";
  width: 14px;
  height: 14px;
  border: none;
  border-top: 3px solid;
  border-left: 3px solid;
  border-radius: 3px;
  margin-left: 5px;
  margin-right: 5px;
  display: inline-block;
  transform: rotate(.37turn);
}

.tui-ico-last:after {
  content: "";
  width: 14px;
  height: 14px;
  border: none;
  border-top: 3px solid;
  border-left: 3px solid;
  border-radius: 3px;
  display: inline-block;
  transform: translateX(-10px)rotate(.37turn);
}

.tui-page-btn.tui-next-is-ellip.tui-last-child:hover, .tui-page-btn.tui-prev-is-ellip.tui-first-child:hover {
  filter: drop-shadow(0 4px 4px #00000040);
  background-color: #fff;
}

.tui-page-btn.tui-next-is-ellip.tui-last-child:active {
  color: #fff;
  background-color: #eac645;
}

.tui-page-btn.tui-prev-is-ellip.tui-first-child:active {
  color: #fff;
  background-color: #111;
}

