@mixin retina {

    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        @content;
    }
}

//* Mobile
@mixin maxW-Phone {
    @media screen and (max-width: $phoneMax) {
        @content;
    }
}

@mixin minW-Phone {
    @media screen and (min-width: $phoneMin) {
        @content;
    }
}


//* Tablet

@mixin maxW-Tablet {
    @media screen and (max-width: $tabletMax) {
        @content;
    }
}

@mixin minW-Tablet {
    @media screen and (min-width: $tabletMin) {
        @content;
    }
}


//* Desktop

@mixin maxW-Dt {
    @media screen and (max-width: $desktopMax) {
        @content;
    }
}

@mixin minW-Dt {
    @media screen and (min-width: $desktopMin) {
        @content;
    }
}

//* Custom media

@mixin minW($widthValue) {
    @media screen and (min-width: $widthValue) {
        @content;
    }
}

@mixin maxW($widthValue) {
    @media screen and (max-width: $widthValue) {
        @content;
    }
}

// Шаг конём
@mixin min-maxW-Dt {
    @media screen and (min-width: $desktopMin) and (max-width: $desktopMax) {
        @content;
    }
}